/* --------- Animation : dialog --------- */
/* --------- CSS Properties --------- */
/* stylelint-disable color-no-hex */
/* stylelint-disable value-keyword-case */
.cq-dialog {
  width: 344px;
  left: calc(50% - 344px / 2);
  top: auto;
  border-radius: 5px;
  position: absolute;
  opacity: 0;
  margin-top: 120px;
  box-shadow: 0 0 0.63em 0.03em rgba(0, 0, 0, 0.14);
  visibility: hidden;
  z-index: -3;
  transition: pointer-events 0.2s cubic-bezier(0.64, 0.04, 0.35, 1), opacity 0.17s cubic-bezier(0.64, 0.04, 0.35, 1), z-index 0.17s cubic-bezier(0.64, 0.04, 0.35, 1), -webkit-transform 0.25s cubic-bezier(0.64, 0.04, 0.35, 1);
  transition: pointer-events 0.2s cubic-bezier(0.64, 0.04, 0.35, 1), transform 0.25s cubic-bezier(0.64, 0.04, 0.35, 1), opacity 0.17s cubic-bezier(0.64, 0.04, 0.35, 1), z-index 0.17s cubic-bezier(0.64, 0.04, 0.35, 1);
  transition: pointer-events 0.2s cubic-bezier(0.64, 0.04, 0.35, 1), transform 0.25s cubic-bezier(0.64, 0.04, 0.35, 1), opacity 0.17s cubic-bezier(0.64, 0.04, 0.35, 1), z-index 0.17s cubic-bezier(0.64, 0.04, 0.35, 1), -webkit-transform 0.25s cubic-bezier(0.64, 0.04, 0.35, 1); }
.smartcharts-light .cq-dialog {
    background: #ffffff; }
.smartcharts-dark .cq-dialog {
    background: #2a3052; }
.cq-dialog.open {
    visibility: visible;
    margin-top: 0px;
    opacity: 1;
    z-index: 3; }
/* Mobile Style */
.smartcharts-mobile .cq-dialog {
  left: 10px;
  width: calc(100% - 20px); }
