.ciq-chart-types .cq-menu-dropdown {
  width: 200px;
  box-sizing: border-box; }
.ciq-chart-types .cq-menu-dropdown .body {
    padding: 0px 5px 0px 16px; }
.ciq-chart-types .ciq-list .ciq-list-item {
  padding: 0;
  justify-content: space-between; }
.smartcharts-light .ciq-chart-types .ciq-list .ciq-list-item.active {
    border-right: 2px solid #f4f4f6; }
.smartcharts-dark .ciq-chart-types .ciq-list .ciq-list-item.active {
    border-right: 2px solid #ffffff; }
.ciq-chart-types .ciq-list .ciq-list-item > .left {
    display: inline-flex;
    align-items: center; }
.ciq-chart-types .ciq-list .ciq-list-item > .ciq-aggregate-setting {
    margin-right: 5px; }
.smartcharts-mobile .ciq-chart-types .ciq-toggle-asset-information {
  margin: 15px 15px 0; }
.smartcharts-mobile .ciq-chart-types .ciq-list {
  margin: 0 15px; }
