/* --------- CSS Properties --------- */
/* stylelint-disable color-no-hex */
/* stylelint-disable value-keyword-case */
.ciq-period {
  padding: 0;
  line-height: 1.5em !important; }
.ciq-period > .cq-menu-btn {
    padding: 0 5px; }
.ciq-period > .cq-menu-btn > .bt-priod {
      display: block;
      height: auto;
      line-height: 2em;
      border-radius: 2px;
      box-sizing: border-box;
      text-align: center;
      font-size: 10px; }
.ciq-period > .cq-menu-btn > .bt-priod .ic-priod {
        font-size: 12px;
        border-radius: 4px;
        padding: 3px 5px; }
.ciq-period.stxMenuActive .bt-priod {
    font-weight: 500; }
.smartcharts-light .ciq-period.stxMenuActive .ic-subtitle, .smartcharts-light
  .ciq-period.stxMenuActive .ic-priod span {
    color: #5c5c5c; }
.smartcharts-dark .ciq-period.stxMenuActive .ic-subtitle, .smartcharts-dark
  .ciq-period.stxMenuActive .ic-priod span {
    color: #ffffff; }
.ciq-period:after {
    display: none; }
.ciq-period .unit_display {
    text-transform: capitalize; }
.ciq-period .interval_display {
    margin: 0 5px 0 0; }
.ciq-period .cq-menu-dropdown {
    width: 296px;
    height: 160px;
    font-size: 12px;
    font-weight: normal; }
.ciq-period .cq-menu-dropdown .cq-interval {
      height: 160px;
      width: 100%;
      display: flex;
      flex-direction: row; }
.smartcharts-light .ciq-period .cq-menu-dropdown .cq-interval {
        background: #ffffff; }
.smartcharts-dark .ciq-period .cq-menu-dropdown .cq-interval {
        background: #353b5b; }
.ciq-period .cq-menu-dropdown .timeUnit {
      display: flex;
      flex-direction: column;
      width: 88px;
      height: 100%; }
.smartcharts-light .ciq-period .cq-menu-dropdown .timeUnit {
        background: #ffffff; }
.smartcharts-dark .ciq-period .cq-menu-dropdown .timeUnit {
        background: #2a3052; }
.ciq-period .cq-menu-dropdown .timeUnit span {
        cursor: pointer;
        display: block;
        height: 40px;
        padding-left: 16px;
        line-height: 40px;
        font-weight: 300; }
.smartcharts-light .ciq-period .cq-menu-dropdown .timeUnit span {
          color: #5c5c5c;
          border-right: 1px solid #e9e9ed; }
.smartcharts-dark .ciq-period .cq-menu-dropdown .timeUnit span {
          color: #aaacba;
          border-right: 1px solid #4a4f6c; }
.ciq-period .cq-menu-dropdown .timeUnit span.selected {
          font-weight: 500; }
.smartcharts-light .ciq-period .cq-menu-dropdown .timeUnit span.selected {
            color: #2a3052;
            background: #f4f4f6;
            border-right: 2px solid #2a3052; }
.smartcharts-dark .ciq-period .cq-menu-dropdown .timeUnit span.selected {
            color: #ffffff;
            background: #4a4f6c;
            border-right: 2px solid #ffffff; }
.ciq-period .cq-menu-dropdown .interval {
      display: flex;
      flex-direction: column;
      width: 208px; }
.ciq-period .cq-menu-dropdown .interval .row {
        height: 40px;
        display: flex;
        flex-direction: row;
        margin: 0 4px;
        align-items: center;
        font-weight: 300; }
.ciq-period .cq-menu-dropdown .interval .row span {
          cursor: pointer;
          margin: 0 3px 0 0;
          width: 24px;
          height: 24px;
          text-align: center;
          line-height: 26px;
          border: 1px solid transparent;
          border-radius: 4px; }
.ciq-period .cq-menu-dropdown .interval .row span.selected {
            pointer-events: none;
            line-height: 27px;
            font-weight: bold; }
.smartcharts-light .ciq-period .cq-menu-dropdown .interval .row span.selected {
              color: #ffffff !important;
              background: #2a3052;
              border-color: #2a3052; }
.smartcharts-dark .ciq-period .cq-menu-dropdown .interval .row span.selected {
              color: #2a3052 !important;
              background: #ffffff;
              border-color: #ffffff; }
.ciq-period .cq-menu-dropdown .interval .row span:hover {
            line-height: 27px;
            font-weight: bold; }
.smartcharts-light .ciq-period .cq-menu-dropdown .interval .row span:hover {
              color: #2a3052;
              border-color: #2a3052; }
.smartcharts-dark .ciq-period .cq-menu-dropdown .interval .row span:hover {
              color: #aaacba;
              border-color: #ffffff; }
.cq-chart-control-left .ciq-period > .cq-menu-btn > .bt-priod .ic-priod {
  padding: 6px 20px; }
/* Mobile Style */
.smartcharts-mobile .ciq-period {
  padding: 0 !important; }
.smartcharts-mobile .ciq-period > .cq-menu-btn {
    padding: 0 !important; }
.smartcharts-mobile .ciq-period > .cq-menu-btn .bt-priod > span {
      padding: 5px !important; }
.smartcharts-480 .cq-chart-control-bottom .ciq-period {
  width: 70px !important;
  top: 0px; }
.smartcharts-480 .cq-chart-control-bottom .ciq-period > .cq-menu-btn {
    padding: 16px 5px !important; }
.smartcharts-900 .cq-chart-control-bottom .ciq-period {
  width: 80px !important;
  top: 0px; }
.smartcharts-900 .cq-chart-control-bottom .ciq-period > .cq-menu-btn {
    padding: 16px 5px !important; }
.smartcharts-1280 .cq-chart-control-bottom .ciq-period {
  width: 100px !important;
  top: 0px; }
.smartcharts-1280 .cq-chart-control-bottom .ciq-period .bt-priod {
    padding-top: 8px; }
