.cq-chart-setting .cq-setting-container .cq-setting-main,
  .cq-chart-setting .cq-setting-container .cq-setting-language {
    width: 260px;
    position: absolute;
    top: 0px;
    transition: opacity 0.17s ease-out, -webkit-transform 250ms ease-out;
    transition: transform 250ms ease-out, opacity 0.17s ease-out;
    transition: transform 250ms ease-out, opacity 0.17s ease-out, -webkit-transform 250ms ease-out; }
.cq-chart-setting .cq-setting-container .cq-setting-main {
